#carouselExampleDark {
  position: relative;
}

.buttons-carousel {
  justify-content: flex-start;
  display: flex;
  position: absolute;
  transform: translateY(-160px);
}

.carousel-indicators [data-bs-target] {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}

.carousel-dark .carousel-indicators [data-bs-target] {
  background-color: #fff;
}

.title-carousel {
  width: 40%;
  text-align: left;
  position: absolute;
  transform: translateY(-120%);
}

.title-carousel p {
  color: #fff;
  font-family: sans-serif;
  font-size: 40px;
  font-weight: bold;
}

.carousel-inner {
  height: 100vh;
  max-height: 100vh;
  background-position: top;
  margin: -100px 0 0;
}

.carousel-item {
  width: 100%;
  height: 100%;
}

.carousel-item img {
  height: 100%;
  width: 100%;
}

